.card {
  width: 100%;
  height: fit-content;
  background: white;
  border-radius: 16px;
  margin-top: 15px;
  padding: 4px
}

.labelColor {
  color: #1e2528;
  font-size: 13px;
  font-weight: 500;
  line-height: 18px;
  text-align: left;
}

.breadcrumbs {
  color: #787c7e;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
}
.RejectedNotification {
  min-height: 60px !important;
  border-radius: 12px;
  border: 0.5px solid #e2c3c3;
  background: #f5e7e7;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}
.ApprovedNotification {
  min-height: 60px !important;
  align-items: center;
  display: flex;
  margin-bottom: 20px;
  min-height: 50px;
  border-radius: 12px;
  border: 0.5px solid #86daa8;
  background: #d9f5e4;
}
.rejectedheader {
  color: #992929;
  font-weight: 600;
  margin-left: 10px;
  font-size: 14px;
}
.viewReason {
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  color: #090909;
  margin-bottom: 0px;
  padding-left: 35px;
}
.approvedheader {
  color: #156d39;
  font-weight: 600;
  margin-left: 10px;
  font-size: 14px;
}
.switchBtn {
  margin-left: 20px;
  border-radius: 4px;
  background: #fff;
  display: flex;
  width: 59px;
  padding: 4px 0px;
  justify-content: space-between;
  align-items: center;
  .switchvalue {
    color: #1e2528;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
  }
  img {
    cursor: pointer;
  }
}
.ViewResonbtn {
  color: #522987;
  text-align: right;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  outline: none;
  border: none;
  margin: 0px 15px;
  background-color: transparent;
  background: transparent;
}
